/** @jsx jsx */
import React from "react";
import { Helmet } from "react-helmet";
import { Styled, ThemeProvider, jsx, Flex, Box } from "theme-ui";
import { Link } from "gatsby";

import components from "./MdxComponents";
import Header from "./Header";
import BrainNote from "./BrainNote";
import Footer from "./Footer";
import ReferredBlock from "./ReferredBlock";
import Popover from "./Popover";

import theme from "../theme";
const BrainNotesContainer = ({ slug, note, location, siteMetadata }) => {
  const popups = {};
  if (note.outboundReferenceNotes) {
    note.outboundReferenceNotes
      .filter((reference) => !!reference.childMdx.excerpt)
      .forEach((ln, i) => {
        popups[ln.slug] = <Popover reference={ln} />;
      });
  }
  const AnchorTagWithPopups = (props) => (
    <components.a {...props} popups={popups} />
  );

  return (
    <ThemeProvider
      theme={theme}
      components={{ ...components, a: AnchorTagWithPopups }}
    >
      <Flex
        sx={{
          flexDirection: "column",
          maxWidth: "1036px",
          margin: "0 auto",
          // height: "100vh",
          // minHeight: "100vh",
        }}
      >
        <Helmet>
          <meta charSet="utf-8" />
          <title>
            {note.title} - {siteMetadata.title}
          </title>
        </Helmet>
        <Header siteMetadata={siteMetadata} />
        <Flex
          sx={{
            flex: 1,
            flexGrow: 1,
            // overflowX: [null, "auto"],
            // overflowY: "scroll",
          }}
        >
          <BrainNote note={note} />
          <ReferredBlock references={note.inboundReferenceNotes} />
        </Flex>
        <Footer />
      </Flex>
    </ThemeProvider>
  );
};

export default BrainNotesContainer;
